.aboutHome {
  height: auto;
  margin-bottom: 100px;
  background-image: url("../../../public/images/LDK1.jpg");
  background-size: cover;
  background-position-y: center;
}

.aboutHome .left img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.aboutHome .item {
  margin-top: 30px;
  padding: 20px;
  transition: 0.5s;
}
.aboutHome .item h2 {
  font-size: 20px;
}
.aboutHome .item p {
  color: #999999;
}
.aboutHome .item:hover {
  color: #fff;
  cursor: pointer;
  box-shadow: 0 5px 25px -2px rgb(0 0 0 / 6%);
}
.aboutHome .item:hover p {
  color: #fff;
}
